/* Features section */

.features__section {
    @extend %section;

    position: relative;
    
    &::after {
        position: absolute;
        content: "";
        display: block;
        
        bottom: 0;
        left: 50%;
        
        width: 1170px;
        height: 1px;
        
        transform: translateX(-50%);
        background-color: #ECECEC;
    }
}
.features__main-title {
    @extend %section-title, %visually-hidden;
}

.features__container {
    @extend %container;
}

.features__list {
    @extend %list;
    display: flex;
}
    
.features__item:not(:last-child) {
    margin-right: 30px;
}
    
.features__title {
    @extend %uptitle;
    
    margin-bottom: 10px;
    color: $primary-text-color;
}
    
.icon-features {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    
    width: 270px;
    height: 120px;
    
    background-color: $secondary-bg-color;
    border-radius: 4px;
}
    
.features__text {
    margin-bottom: 0;
    
    line-height: 1.71;
    color: $secondary-text-color;
}