%logo {
font-family: 'Raleway', sans-serif;
font-weight: 700;
font-size: 26px;
line-height: 1.19;
letter-spacing: 0.03em;
}

%list {
list-style: none;
margin: 0;
padding-left: 0;
}

%link {
text-decoration: none;
color: var(--primary-text-color);
}

%section {
padding-top: 94px;
padding-bottom: 94px;
}

%container {
margin-left: auto;
margin-right: auto;
padding-left: 15px;
padding-right: 15px;
max-width: 1200px;
/* outline: 2px solid red; */
}

%button {
display: inline-block;
font-size: 16px;
text-decoration: none;
text-align: center;
cursor: pointer;

border-radius: 4px;
border-color: transparent;
}

%visually-hidden {
position: absolute !important;
clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
clip: rect(1px, 1px, 1px, 1px);
padding:0 !important;
border:0 !important;
height: 1px !important; 
width: 1px !important; 
overflow: hidden;
}

%uptitle {
margin-top: 0;
    
font-size: inherit;
font-weight: 700;
text-transform: uppercase;
}

%section-title {
margin-top: 0;
margin-bottom: 50px;

font-weight: 700;
font-size: 36px;
line-height: 1.17;
text-align: center;

color: var(--primary-text-color);
}